@import "./../../sass/variables.scss";
@import "../../sass/animations.scss";

.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;

  background-image: url(../../img/logo-02.1c.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-position: center;

  &-section {
    &--1 {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin: 10rem 5rem 0rem 5rem;

      &_title {
        margin: 2rem;
        color: $color-purple;
        font-weight: 700;
        font-size: 5rem;
        text-transform: uppercase;
      }
      &_text {
        text-align: center;
        margin: 3rem 0 5rem 0;
        font-size: 2.3rem;
      }
    }
    &--container {
      display: flex;
      justify-content: center;
      width: 100%;

      &_1 {
        flex: 1;
      }
      &_2 {
        flex: 1;
      }
    }
    &--2,
    &--3 {
      text-align: justify;
      background-color: rgba(0, 0, 0, 0.407);
      border-radius: 2rem;
      box-shadow: 10px 5px 5px rgb(0, 0, 0);

      &_title {
        margin: 3rem;
        text-align: center;
        color: $color-purple;
        font-weight: 700;
      }
      &_text {
        margin: 1rem 0;
      }
    }

    &--2 {
      padding: 1rem 5rem 5rem 5rem;
      margin: 0rem 20rem 5rem 10rem;

      // &_title {
      // }
      // &_text {
      // }
    }

    &--2b {
      background-color: rgba(255, 255, 255, 0.118);
      border-radius: 1rem;
      // box-shadow: 10px 5px 5px rgb(0, 0, 0);
      padding: 1rem 1rem 1rem 1rem;
      margin: 0rem 20rem 5rem 10rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      &_video {
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
      }
      &_title--1,
      &_title--2 {
        margin: 0.1rem 0.5rem;
        font-family: daniel;
        font-weight: 700;
        letter-spacing: 0.3rem;
      }
      &_title--1 {
        color: $color-purple;
        text-align: start;
      }
      &_title--2 {
        margin-top: 1rem;
        text-align: end;
      }
    }

    &--3 {
      padding: 1rem 5rem 5rem 5rem;
      margin: 0rem 10rem 2rem 20rem;

      &_title {
        color: $color-purple;
        font-weight: 700;
      }
      &_text {
      }
    }
    &--4 {
      margin: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      &_team {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        padding: 0 15rem 0 20rem;
      }

      &_title {
        margin: 3rem 0rem 3rem 7rem;
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 0.3rem;
        font-family: danielbd;
        font-weight: 900;
        font-size: 3.5rem;
      }
      &_person {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 2rem;

        &__img {
          margin: 0 3rem;
          object-fit: contain;
          background-color: $color-white;
          border-radius: 50%;
          height: 13rem;
          width: 13rem;
          box-shadow: 10px 5px 5px rgb(0, 0, 0);
          border: solid $color-purple 4px;
        }
        &__text {
          animation: moveInLeftStart 1s;
        }
        &__name {
          font-size: 2.5rem;
        }
        &__job {
          font-style: italic;
          font-size: 2rem;
        }
      }
    }
  }
}
//**** MEDIA QUERIES

@media only screen and (max-width: 1300px) {
  .about {
    background-image: url(../../img/logo-blured.jpg);

    &-section {
      &--container {
        flex-direction: column;
        justify-content: center;
      }
      &--2,
      &--3 {
        text-align: center;
        margin: 0rem 5rem 5rem 5rem;
        &_text {
          font-size: 2rem;
        }
      }

      &--2b {
        margin: 0rem 5rem 5rem 5rem;
      }

      &--3 {
        margin: 0rem 5rem 5rem 5rem;
      }
      &--4 {
        margin-bottom: 5rem;

        &_team {
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          margin-bottom: 5rem;
          padding: 0;
        }

        &_title {
          margin: 3rem 0rem 3rem 0rem;
          letter-spacing: 0.4rem;
        }
        &_person {
          flex-direction: column;

          &__img {
            margin: 1rem 2rem;
            height: 13rem;
            width: 13rem;
          }
          &__text {
            animation: none;
            text-align: center;
          }
          &__name {
            font-size: 2.5rem;
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 800px) {
  .about {
    &-section {
      &--container {
        align-items: center;
      }

      &--4 {
        &_team {
          flex-direction: column;
        }

        &_title {
          font-size: 3rem;
        }
        &_person {
          &__img {
            height: 14rem;
            width: 14rem;
          }
          &__name {
            font-size: 3rem;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 500px) {
  .about {
    &-section {
      &--1 {
        &_title {
          font-size: 3rem;
        }
        &_text {
          font-size: 1.5rem;
        }
      }

      &--2,
      &--3 {
        padding: 1rem 2.5rem 5rem 2.5rem;
        &_title {
          font-size: 2.5rem;
        }
        &_text {
          font-size: 1.5rem;
        }
      }

      &--2b {
        &_title--1,
        &_title--2 {
          font-size: 1.5rem;
        }

        &_title--2 {
          text-align: end;
        }
      }

      &--4 {
        &_title {
          font-size: 2rem;
        }
        &_person {
          &__name {
            font-size: 2rem;
          }
          &__job {
            font-size: 1.8rem;
          }
        }
      }
    }
  }
}
